import React, { useState } from "react";
import logos from "../../assets/pictures/logos.png";
import {
  FooterContainer,
  Contact,
  Logos,
  CardFooterContainer,
} from "./footerStyle";
import SecBanner from "./banner/SecBanner";

export default function Footer({ card }) {
  const [textPop, setTextPop] = useState(false);
  const [showPopUp, setShowPopUp] = useState(false);
  const containerStyle = {
    display: "flex",
    flexDirection: "row-reverse",
    textAlign: "center",
    color: "white",
    marginTop: "0",
    fontWeight: "bold",
    fontSize: "0.7rem",
    padding: "2rem 0.5rem",
  };
  const unselectedStyle = {
    margin: "auto",
    borderRadius: "9px",
    marginLeft: "10px",
    marginRight: "10px",
    width: "4.8rem",
    padding: "2px 0px",
    justifyContent: "space-around",
    display: "flex",
    alignItems: "center",
    background: "none",
    border: "1px solid #657c54",
  };
  const labels = [
    {
      name: "",
      icon: require("../../assets/pictures/odot.svg"),
      title: `"   `,
      text: (
        <>
              - ,      
           .
          <br />
          <p style={{ marginTop: "0.5rem" }} />
                 ,  
                    
             .
          <br />
          <p style={{ marginTop: "0.5rem" }} />
               '50     -50' -  
          -    '',     " 
          " "  , " '  .
          <br />
          <p style={{ marginTop: "0.5rem" }} />
                    
          ,     .
          <br />
                     
          <p style={{ marginTop: "0.5rem" }} />
          -            .
          <br />
          <p style={{ marginTop: "0.5rem" }} />
           !
        </>
      ),
    },
  ];

  return (
    <>
      {card ? (
        ""
      ) : (
        <FooterContainer>
          <Logos src={logos} />
            "  ,   "
          <div
            style={{ direction: "rtl", display: "flex", alignItems: "stretch" }}
          >
            <img
              src={require("../../assets/pictures/email.png")}
              style={{ width: "15px", height: "15px", marginLeft: "5px" }}
            />
             {" "}
            <a
              style={{
                marginRight: "5px",
                textDecoration: "none",
                color: "white",
              }}
              href="mailto:artechb15@gmail.com"
            >
              artechb15@gmail.com
            </a>
          </div>
          <div style={containerStyle}>
            {labels.map((label) => (
              <div
                key={label}
                style={unselectedStyle}
                onClick={() => {
                  setShowPopUp(true);
                  setTextPop(label);
                }}
              >
                <img
                  src={label.icon}
                  style={{ width: "15px", height: "15px" }}
                ></img>
                {label.name}
              </div>
            ))}
          </div>
        </FooterContainer>
      )}
      {showPopUp && <SecBanner setShowPopUp={setShowPopUp} label={textPop} />}
    </>
  );
}
